@import url("reset.css");

.container {
    max-width: 1100px;
    margin: 0px auto;
    padding-top: 20px;
  min-height: 100vh;
}

.form{
  border: solid 2px blue;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px 0px;
  &__input-wrap{

  }
  &__label{
    color: saddlebrown;
    font-size: 20px;
    margin-right: 10px;

  }
  &__input-count{
    padding: 5px;
    max-width: 50px;
    border-radius: 3px;
  }
}
.body {
  background-color: #0fabed;
}
.button{
  padding: 5px;
  background-color: orange;
  border-radius: 5px;
  margin-top: 10px;
  //flex: 0 0 10%;
  max-width: 20%;
  color: saddlebrown;
  font-size: 18px;
  &:hover{
    cursor: pointer;
    background-color: gold;

  }
}

.box-drag{
  display: flex;
  &__el
  {
    cursor: move;
    &--sun{
      max-height: 100px;
      max-width: 100px;
    }
  }

}
.container {
      display: flex;
      flex-direction: column;
}
.element {
      
display: flex;
      justify-content: space-between;
}


